import uniPopup from '@/components/uni-popup/uni-popup.vue'
import jyfParser from '@/components/jyf-parser/jyf-parser';
export default {
	components: {uniPopup,jyfParser},
	data() {
		return {
			time:6,
			timer:null,
			top:32,
			detail:{},
			showBtn:false
		}
	},
	onLoad(params) {
		if(params.type){
			this.showBtn=true
		}
		this.top=uni.getMenuButtonBoundingClientRect().top
		let detail=JSON.parse(params.detail)
		detail.content=decodeURIComponent(detail.content)
		this.detail=detail
		console.log(this.detail)
	},
	onUnload(){
		this.timer&&clearTimeout(this.timer)
	},
	methods: {
		//供奉
		goWorship(){
			let detail={...this.detail}
			detail.content=encodeURIComponent(detail.content)
			uni.reLaunch({
				url:'/pages/package/package?detail='+JSON.stringify(detail)
			})
