			// 	console.log(res)
			// 	uni.navigateTo({
			// 		url:'/pages/package/package?detail='+JSON.stringify(this.detail)+'&status=1'
			// 	})
			// }).catch((err)=>{
			// 	if(uni.getStorageSync('first')==0){
			// 		//窮鬼沒氪過金 第一次來
